<template>
    <div class="container" ref="top">
        <h3 style="text-align: center;padding-bottom:20px;padding-top:20px;">{{ name }}</h3>
        <div class="row">
            <div v-for="(item, index) in content" :key="index" class="col-md-6 offset-md-3">
                <span class="paragraph">{{ item }}</span>
            </div>
        </div>
        <div class="bottom">
            <div class="row justify-content-center" style="text-align: center;">
                <div class="col-5">
                    <!-- <button >返回首页</button> -->
                    <router-link to="/">返回首页</router-link>
                </div>
                <div class="col-5">
                    <span style="color: #007bff" @click="randomContent">随机抽取</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import http from '../axios';
import { bid } from '../constant';

export default {
    name: "readcolor",
    setup() {

    },
    data() {
        return {
            cid: this.$route.params.cid,
            name: this.$route.params.name,
            content: [],
        }
    },
    computed: {

    },
    created() {
        this.fetchContent()

    },
    methods: {
        async fetchContent() {
            this.content = []
            var res = await http.get("/book/chapter/" + this.cid)
            var content = res.data['content']

            this.content = content.split('\n')
        },

        async randomContent() {
            var res = await http.get("/color/" + bid + "/random")
            let data = res.data
            this.name = data.title
            this.content = data.content.split('\n')
            this.$refs.top.scrollIntoView()
        }


    },

}

</script>

<style>
.paragraph {
    font-size: 18px;
    font-family: Georgia, "Times New Roman", Times, serif;
    text-indent: 2em;
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
}
.bottom {
    padding-top: 40px;
    padding-bottom: 40px;
}
</style>